import {
  _decorator,
  Button,
  Component,
  EventHandler,
  instantiate,
  Label,
  Node,
  Prefab,
  ProgressBar,
} from "cc";
import { basePageView } from "../common/basePageView";
import { taskListResponseType } from "../invite/inviteModel";
import GlobalData from "../home/GloabalClass";
const { ccclass, property } = _decorator;

export const twitterTaskId = [9, 10, 11, 12]

export interface TaskData{
    buttonText: string;
    icon: string;
    remark: string;
    showMsg: string;
    taskDesc: string;
    taskId: number;
    taskLink: string;
    taskName: string;
    taskReward: number;
    taskType: number;
    value1: number;
    value2: string;
    status: number
}

export interface TaskDataList {
    taskResponseList: {
    buttonText: string;
    icon: string;
    remark: string;
    showMsg: string;
    taskDesc: string;
    taskId: number;
    taskLink: string;
    taskName: string;
    taskReward: number;
    taskType: number;
    value1: number;
    value2: string;
    status: number
  }[];
  taskModelId: number;
  taskModelName: string;
  taskType: string;
}

export const taskUrls = {
  SearchTask: "https://api.infinitytest.cc/api/v1/task/list",
  ClaimReward: "https://api.infinitytest.cc/api/v1/task/claim",
  CompleteTask: "https://api.infinitytest.cc/api/v1/task/complete",
  CheckIn: "https://api.infinitytest.cc/api/v1/task/checkIn",
};

@ccclass("earnView")
export class earnView extends basePageView {
  @property(Prefab)
  social_prefab: Prefab;

  @property(Prefab)
  daily_prefab: Prefab;

  @property(Node)
  social_parent: Node;

  @property(Node)
  daily_parent: Node;

  @property(Node)
  discoverPage: Node;

  @property({ type: [Node] })
  check_btns: Node[] = [];
  taskInfo: taskListResponseType;

  start() {}

  async completeTask(id: number) {
    var config = {
      method: "post",
      url: taskUrls.CompleteTask,
      headers: {
        "User-Agent": "Apifox/1.0.0 (https://apifox.com)",
        Authorization: `Bearer ${GlobalData.token}`,
      },
      params: {
        taskId: id,
        // 添加更多参数
      },
    };

    await (window.axios as any)(config)
      .then(() => {
      })
      .catch((error) => {
        console.log(error);
      });
  }

  async checkIn(index: number) {
    var config = {
      method: "post",
      url: taskUrls.CheckIn,
      headers: {
        "User-Agent": "Apifox/1.0.0 (https://apifox.com)",
        Authorization: `Bearer ${GlobalData.token}`,
      },
    };

    await (window.axios as any)(config)
      .then((taskList: taskListResponseType) => {
        if (taskList.data.code === "90000") {
          this.changeCheckTaskState(index);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

  async claimReward(id: number) {
    var config = {
      method: "post",
      url: taskUrls.ClaimReward,
      headers: {
        "User-Agent": "Apifox/1.0.0 (https://apifox.com)",
        Authorization: `Bearer ${GlobalData.token}`,
      },
      params: {
        taskId: id,
        // 添加更多参数
      },
    };

    await (window.axios as any)(config)
      .then((taskList: taskListResponseType) => {

      })
      .catch((error) => {
        console.log(error);
      });
  }

  initTaskNode(info: taskListResponseType) {
    this.taskInfo = info;
    info.data.data.checkInList.forEach((task, index) => {
      this.setCheckTaskNode(task, index);
      this.check_btns[index].on(
        Button.EventType.CLICK,
        async () => {
          await this.clickCheckTaskNode(index);
        },
        this
      );
    });

    info.data.data.taskModelResponses.forEach((taskListPart) => {
      if (taskListPart.taskModelName === "Social tasks") {
        this.initSocialTaskNode(taskListPart as unknown as TaskDataList);
      }
      if (taskListPart.taskModelName === "Daily tasks") {
        this.initDailyTaskNode(taskListPart as unknown as TaskDataList);
      }
    });
  }

  initSocialTaskNode(info: TaskDataList) {
    info.taskResponseList.forEach((task, index) => {
      const child = instantiate(this.social_prefab);
      this.updateSocialTaskUiByState(task, child)
      child.on(Button.EventType.CLICK, async () => {
        if(this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index].status === 3){
            return
        }
        if(!(twitterTaskId.indexOf(this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index].taskId) && GlobalData.taskData.data.data.twitterUserName)){
            window.Telegram.WebApp.showAlert({
                message: 'please bind twitter!'
              }, () => {
                // 用户点击关闭按钮后调用的回调函数
                console.log('警告框已关闭');
              });
            return
        }
        if(this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index].status === 1){
            window.Telegram.WebApp.openLink(task.taskLink, { tryInstantView: true });
            await this.completeTask(task.taskId)
            this.changeDailyOrSocialTaskState(2,this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index],index, child)
        }
        if(this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index].status === 2){
            await this.claimReward(task.taskId)
            this.changeDailyOrSocialTaskState(2,this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index],index, child)
        }
      }, this);

      this.social_parent.addChild(child);
    });
  }

  initDailyTaskNode(info: TaskDataList) {
    info.taskResponseList.forEach((task, index) => {
      const child = instantiate(this.daily_prefab);
      this.updateDailyTaskUiByState(task, child)
      child.on(Button.EventType.CLICK, async () => {
        if(this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index].status === 3){
            return
        }
        if(this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index].status === 1){
            //跳转大厅
            this.discoverPage.setPosition(0, 0)
            this.node.setPosition(0,0)
            await this.completeTask(task.taskId)
            this.changeDailyOrSocialTaskState(3,this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index],index,child)
        }
        if(this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index].status === 2){
            await this.claimReward(task.taskId)
            this.changeDailyOrSocialTaskState(3,this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index],index,child)
        }
      }, this);

      this.daily_parent.addChild(child);
    });
  }

  async clickCheckTaskNode(index: number) {
    await this.checkIn(index);
  }

  setCheckTaskNode(index, info) {
    if(info.status === 1){
        this.changeCheckTaskState(index)
    }
  }

  changeCheckTaskState(index: number) {
    this.check_btns[index].getChildByName('count').getComponent(Label).string = String(GlobalData.taskData.data.data.checkInList[index].point)
    this.check_btns[index].getChildByName('uncoplete').active = false
    this.check_btns[index].getChildByName('complete').active = true
    this.check_btns[index].getChildByName('check').active = true
    this.check_btns[index].getChildByName('dollar').active = false
  }

  changeDailyOrSocialTaskState(type, info: TaskData, index, node: Node) {
    if (type === 2) {
        this.taskInfo.data.data.taskModelResponses[0].taskResponseList[index].status = info.status 
        this.updateSocialTaskUiByState(info, node)       
        //改变ui
    }
    if (type === 3) {
        this.taskInfo.data.data.taskModelResponses[1].taskResponseList[index].status = info.status
                //改变ui
        this.updateDailyTaskUiByState(info, node)  
    }


  }

  updateDailyTaskUiByState(info: TaskData, node) {
    node.getChildByName('count').getComponent(Label).string = info.taskReward
    node.getChildByName('desc').getComponent(Label).string = info.taskDesc
    //node.getChildByName('ProgressBar').getComponent(ProgressBar).value = info
    // if(info.status === 1){
    //     node.getChildByName('un_complete_bg').active = true
    //     node.getChildByName('complete_bg').active = false
    //     node.getChildByName('dollar').active = true
    //     node.getChildByName('count').active = true
    //     node.getChildByName('check').active = false
    //     //node.getChildByName('desc')
    // }
    if(info.status === 1){
        node.getChildByName('un_complete_bg').active = false
        node.getChildByName('complete_bg').active = true
        node.getChildByName('dollar').active = true
        node.getChildByName('count').active = true
        node.getChildByName('check').active = false
    }
    if(info.status === 2){
        node.getChildByName('un_complete_bg').active = false
        node.getChildByName('complete_bg').active = true
        node.getChildByName('dollar').active = false
        node.getChildByName('count').active = false
        node.getChildByName('check').active = true
    }
  }

  updateSocialTaskUiByState(info: TaskData, node) {
    node.getChildByName('count').getComponent(Label).string = info.taskReward
    node.getChildByName('desc').getComponent(Label).string = info.taskDesc
    // if(info.status === 1){
    //     node.getChildByName('uncomplete').active = true
    //     node.getChildByName('complete').active = false
    //     node.getChildByName('dollar').active = true
    //     node.getChildByName('count').active = true
    //     node.getChildByName('check').active = false
    //     //node.getChildByName('desc')
    // }
    if(info.status === 1){
        node.getChildByName('uncomplete').active = false
        node.getChildByName('complete').active = true
        node.getChildByName('dollar').active = true
        node.getChildByName('count').active = true
        node.getChildByName('check').active = false
    }
    if(info.status === 2){
        node.getChildByName('uncomplete').active = false
        node.getChildByName('complete').active = true
        node.getChildByName('dollar').active = false
        node.getChildByName('count').active = false
        node.getChildByName('check').active = true
    }
  }

  update(deltaTime: number) {}
}
